import React from 'react';
import { View, StyleSheet } from 'react-native';
import HammerAnimation from './HammerAnimation';
import colors from '../../theme/colors';
import { AppLoadingOverlayProps } from '../../redux/features/app/types';
import { useSelector } from 'react-redux';
import { RootState } from '../../redux/store/types';
import { SCREEN_WIDTH } from 'tradewize-component';

const LoadingOverlay = () => {
  const {
    size = SCREEN_WIDTH * 0.2,
    center = false,
    isVisible = false,
  }: AppLoadingOverlayProps = useSelector(
    (state: RootState) => state.app.loadingOverlay,
  );

  if (!isVisible) {
    return null;
  }

  return (
    <View style={[styles.container, center && styles.containerCenter]}>
      <View style={[center && styles.logoContainerCenter]}>
        <HammerAnimation size={size} />
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: colors.overlayWhite07,
    zIndex: 1000
  },
  containerCenter: {
    justifyContent: 'center',
    alignItems: 'center',
  },
  logoContainerCenter: {
    position: 'relative',
    top: 'auto',
    flex: 1,
    justifyContent: 'center',
  },
});

export default LoadingOverlay;
